import {hashHistory, browserHistory} from 'react-router'

export const production = process.env.NODE_ENV === 'production'

export const history = production ? browserHistory : hashHistory

export const domain = production
  ? `http://manager.beanloop.se:7001`
  : `http://localhost:7001`

export const oauthUrl = (service, token) =>
  `${production ? '' : 'http://localhost:7001'}/auth/${service}/callback/${token}`
